import { AppActionTypes } from '../actions/action.types'

const initialState: PriceState = {
  VLXPriceSuccessResponse: 0,
  VLXPriceChange: '+0%',
  VLXPriceDiff: 0,
  VLXPriceLoading: false,
  VLXPriceError: '',
  VLXTotalSupply: 0,

  BTCPriceSuccessResponse: 0,
  BTCPriceLoading: false,
  BTCPriceError: '',

  TOKENPriceSuccessResponse: 2,
  TOKENPriceLoading: false,
  TOKENPriceError: '',
}

export interface PriceState {
  VLXPriceSuccessResponse: number
  VLXPriceChange: string
  VLXPriceDiff: number
  VLXPriceLoading: boolean
  VLXPriceError: string
  VLXTotalSupply: number
  BTCPriceSuccessResponse: number
  BTCPriceLoading: boolean
  BTCPriceError: string

  TOKENPriceSuccessResponse: number
  TOKENPriceLoading: boolean
  TOKENPriceError: string
}

const priceReducer = (state: PriceState = initialState, action: AppActionTypes): PriceState => {
  switch (action.type) {
    case 'GET_TOKEN_PRICE_REQUEST':
      return {
        ...state,
        TOKENPriceLoading: true,
        TOKENPriceError: '',
        TOKENPriceSuccessResponse: action.payload.TOKENPrice,
      }

    case 'GET_TOKEN_PRICE_SUCCESS':
      return {
        ...state,
        TOKENPriceLoading: false,
        TOKENPriceError: '',
        TOKENPriceSuccessResponse: action.payload.TOKENPrice,
      }

    case 'GET_TOKEN_PRICE_ERROR':
      return {
        ...state,
        TOKENPriceLoading: false,
        TOKENPriceError: action.payload.error,
        TOKENPriceSuccessResponse: action.payload.TOKENPrice,
      }

    case 'GET_VLX_PRICE_REQUEST':
      return {
        ...state,
        VLXPriceLoading: true,
        VLXPriceError: '',
      }

    case 'GET_VLX_PRICE_SUCCESS':
      return {
        ...state,
        VLXPriceLoading: false,
        VLXPriceError: '',
        VLXPriceSuccessResponse: action.payload.VLXPrice,
        VLXPriceChange: action.payload.VLXPriceChange,
        VLXPriceDiff: action.payload.VLXPriceDiff,
        VLXTotalSupply: action.payload.VLXTotalSupply,
      }

    case 'GET_VLX_PRICE_ERROR':
      return {
        ...state,
        VLXPriceLoading: false,
        VLXPriceError: action.payload.error,
        VLXPriceSuccessResponse: action.payload.VLXPrice,
        VLXPriceChange: action.payload.VLXPriceChange,
        VLXPriceDiff: action.payload.VLXPriceDiff,
        VLXTotalSupply: action.payload.VLXTotalSupply,
      }

    case 'GET_BTC_PRICE_REQUEST':
      return {
        ...state,
        BTCPriceLoading: true,
        BTCPriceError: '',
      }

    case 'GET_BTC_PRICE_SUCCESS':
      return {
        ...state,
        BTCPriceLoading: false,
        BTCPriceError: '',
        BTCPriceSuccessResponse: action.payload.BTCPrice,
      }

    case 'GET_BTC_PRICE_ERROR':
      return {
        ...state,
        BTCPriceLoading: false,
        BTCPriceError: action.payload.error,
      }

    default:
      return state
  }
}

export default priceReducer
