import React, { useState } from 'react'
import { StyledPageContainer, useStyles } from './style'
import { connect } from 'react-redux'
import { useLocation, useRouteMatch } from 'react-router-dom'
import { getNFTdetail } from '../../../store/actions/token'
import { AppState } from '../../../store/configureStore'
import { getVLXPrice } from '../../../store/actions/price'

import { columns } from './data'
import TableInfo from './components/TableInfo'

import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  Box,
  Grid,
  IconButton,
  SvgIcon,
  Tooltip,
  TooltipProps,
  Paper,
  Container,
  Typography,
  Tabs,
} from '@material-ui/core'

import ActivityTable from './components/Table'

interface ViewTokenProps {
  getNFTdetail: (address: any, id: any, page: any, rowsPerPage: any) => void
  getVLXPrice: () => void
  VLXPrice: number
  nftInfo: any
  activity: any
  totalActivity: any
  loading: any
  requestStatus: boolean
}

function NFT({
  getNFTdetail,
  getVLXPrice,
  VLXPrice,
  nftInfo,
  totalActivity,
  activity,
  loading,
  requestStatus,
}: ViewTokenProps) {
  const classes = useStyles()
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(50)

  const match: any = useRouteMatch(`/nft/:address/:tokenId`)
  const [props, setProps] = useState<any>()

  //   const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage)

  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setRowsPerPage(Number(event.target.value))
    setPage(0)
  }

  const handleChangePage = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
    setPage(newPage)
  }
  React.useEffect(() => {
    setProps(match?.params)
    const data = {
      address: props?.address,
      tokenId: props?.tokenId,
    }
    if (data?.address && data?.tokenId) {
      getNFTdetail(data?.address, data?.tokenId, page, rowsPerPage)
    }
    getVLXPrice()
  }, [page, rowsPerPage, getNFTdetail, props?.tokenId])
  return (
    <StyledPageContainer>
      <Box style={{ marginBottom: '20px' }}>
        <Grid container>
          <Grid item lg={5} md={6} xs={12} className={classes.imageBox}>
            <Box className={classes.subImage}>
              {/* <img src="https://etherscan.io/images/main/empty-token.png" alt="icon" className={classes.imageStyle} /> */}
              <img
                src={
                  nftInfo?.metadata?.image?.slice(0, 16) === 'https://ipfs.io/'
                    ? nftInfo?.metadata?.image?.replace(/ipfs.io/gi, 'gateway.pinata.cloud')
                    : nftInfo?.metadata?.image
                }
                alt="icon"
                className={classes.imageStyle}
              />
            </Box>
          </Grid>
          <Grid item lg={7} md={6} xs={12} style={{ paddingLeft: '20px' }}>
            <Typography component="h1" className={classes.topTitle}>
              {nftInfo?.metadata?.name}
            </Typography>
            <Typography style={{ marginBottom: '10px' }}>{nftInfo?.name}</Typography>
            <Grid container className={classes.priceBox}>
              <Grid item lg={4} md={4} xs={12} className={classes.item}>
                <Typography className={classes.subTitle}>Min. Price</Typography>
                <Typography>
                  {nftInfo?.minPrice ? nftInfo?.minPrice / Math.pow(10, 18) : 0} VLX($
                  {nftInfo?.minPrice ? (nftInfo?.minPrice * VLXPrice) / Math.pow(10, 18) : 0})
                </Typography>
              </Grid>
              <Grid
                item
                lg={4}
                md={4}
                xs={12}
                className={classes.item}
                style={{ borderLeft: '1px solid #e7eaf3', borderRight: '1px solid #e7eaf3' }}
              >
                <Typography className={classes.subTitle}>Last Sale (item)</Typography>
                <Typography>
                  {nftInfo?.lastSale ? nftInfo?.lastSale / Math.pow(10, 18) : 0} VLX ($
                  {nftInfo?.lastSale ? (nftInfo?.lastSale * VLXPrice) / Math.pow(10, 18) : 0})
                </Typography>
              </Grid>
              <Grid item lg={4} md={4} xs={12} className={classes.item}>
                <Typography className={classes.subTitle}>Last Sale (Contract)</Typography>
                <Typography>
                  {nftInfo?.lastSaleContract ? nftInfo?.lastSaleContract / Math.pow(10, 18) : 0} VLX ($
                  {nftInfo?.lastSaleContract ? (nftInfo?.lastSaleContract * VLXPrice) / Math.pow(10, 18) : 0})
                </Typography>
              </Grid>
            </Grid>
            <Box className={classes.boxBorder}>
              <Typography className={classes.detailTxt}> Details</Typography>
              <Grid container className={classes.detailBox}>
                <Grid item lg={5} md={5} sm={12} xs={12}>
                  <Typography className={classes.subTitle} style={{ marginBottom: '1.25rem' }}>
                    Owner:
                  </Typography>
                  <Typography className={classes.subTitle} style={{ marginBottom: '1.25rem' }}>
                    Contract Address:
                  </Typography>
                  <Typography className={classes.subTitle} style={{ marginBottom: '1.25rem' }}>
                    Creator:
                  </Typography>
                  <Typography className={classes.subTitle} style={{ marginBottom: '1.25rem' }}>
                    Token ID:
                  </Typography>
                  <Typography className={classes.subTitle} style={{ marginBottom: '1.25rem' }}>
                    Token Standard:
                  </Typography>
                  <Typography className={classes.subTitle}>Marketplaces:</Typography>
                </Grid>
                <Grid item lg={7} md={5} sm={12} xs={12}>
                  <Typography style={{ marginBottom: '1.25rem' }}>{nftInfo?.owner ? nftInfo?.owner : '-'}</Typography>
                  <Typography style={{ marginBottom: '1.25rem' }}>{props?.address}</Typography>
                  <Typography style={{ marginBottom: '1.25rem' }}>
                    {nftInfo?.creator ? nftInfo?.creator : nftInfo?.name}
                  </Typography>
                  <Typography style={{ marginBottom: '1.25rem' }}>{props?.tokenId}</Typography>
                  <Typography style={{ marginBottom: '1.25rem' }}>{nftInfo?.type}</Typography>
                  <Typography></Typography>
                </Grid>
              </Grid>
            </Box>
          </Grid>
        </Grid>
      </Box>
      <Box>
        <ActivityTable
          tableInfo={() => TableInfo(totalActivity, rowsPerPage, loading)}
          rowsPerPage={rowsPerPage}
          erc721TransferCnt={totalActivity}
          page={page}
          rows={activity}
          loading={loading}
          columns={columns}
          status={requestStatus}
          // emptyRows={emptyRows}
          handleChange={handleChange}
          handleChangePage={handleChangePage}
        />
      </Box>
    </StyledPageContainer>
  )
}

const mapStateToProps = (state: AppState) => ({
  nftInfo: state.token.nftInfo,
  activity: state.token.activity,
  totalActivity: state.token.totalActivity,
  VLXPrice: state.price.VLXPriceSuccessResponse,
})

export default connect(mapStateToProps, { getNFTdetail, getVLXPrice })(NFT)
