import React from 'react'
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles'
import { Grid, Typography, Divider } from '@material-ui/core'
import Bluetool from './bluetooltip'
import commaNumber from 'comma-number'
import { connect } from 'react-redux'
import { AppState } from '../../../../store/configureStore'
import { getVLXPrice, getBTCPrice } from '../../../../store/actions/price'
import { TOTAL_COIN_SUPPLY } from '../../../../config/config'
import { numberWithCommas } from '../../../../common/utils'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      paddingRight: '1.5rem',
      paddingLeft: '1.5rem',
      borderRight: `1px solid ${localStorage.appTheme === 'darkTheme' ? '#323232' : '#e7eaf3'}`,
      lineHeight: 1,
      wordBreak: 'break-word',
      [theme.breakpoints.down('xs')]: {
        borderRight: 'none',
      },
    },
    '&a, a: hover': {
      textDecoration: 'none',
      color: 'blue',
      textDecorationColor: 'blue',
    },
    chip: {
      margin: theme.spacing(0.5),
    },
    divider: {
      backgroundColor: `${localStorage.appTheme === 'darkTheme' ? '#323232' : '#e7eaf3'}`,
      margin: '1.6rem 0 ',
    },
    img: {
      width: ' 1.75rem',
      height: '1.75rem',
      marginRight: '10px',
    },
    text1: {
      color: `${localStorage.appTheme === 'darkTheme' ? '#8c96a5' : '#77838f'}`,
      fontSize: '.76563rem',
      alignSelf: 'end',
    },
    text2: {
      fontSize: '.9375rem',
      color: `${localStorage.appTheme === 'darkTheme' ? '#e5ecf3' : '#1e2022'}`,
      alignSelf: 'end',
      '&:hover': {
        color: '#3498db',
      },
    },
    text3: {
      color: `${localStorage.appTheme === 'darkTheme' ? '#8c96a5' : '#77838f'}`,
      fontSize: '.9375rem',
      marginLeft: '4px',
      alignSelf: 'end',
    },
    priceHigh: {
      fontSize: '80%',
      color: '#00c9a7!important',
      marginLeft: '4px',
      alignSelf: 'center',
    },
    priceLow: {
      fontSize: '80%',
      color: '#de4437!important',
      marginLeft: '4px',
      alignSelf: 'center',
    },
  }),
)

interface PriceProps {
  getVLXPrice: () => void
  getBTCPrice: () => void
  VLXPriceLoading: boolean
  VLXPriceError: string
  VLXPrice: number
  VLXPriceChange: string
  VLXPriceDiff: number
  VLXTotalSupply: number
  BTCPriceLoading: boolean
  BTCPriceError: string
  BTCPrice: number
}

function Price({
  getVLXPrice,
  getBTCPrice,
  VLXPriceLoading,
  VLXPriceError,
  VLXPrice,
  VLXPriceChange,
  VLXPriceDiff,
  BTCPriceLoading,
  VLXTotalSupply,
  BTCPriceError,
  BTCPrice,
}: PriceProps) {
  const classes = useStyles()

  React.useEffect(() => {
    getVLXPrice()
    getBTCPrice()
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  // React.useEffect(() => {
  //   console.log('VLXPrice', VLXPrice)
  //   console.log('BTCPrice', BTCPrice)
  // }, [VLXPriceLoading, BTCPriceLoading])
  return (
    <div className={classes.root}>
      <Grid container alignItems="center">
        <Grid>
          <img src="/images/Home/HomeBanner/price.jpg" className={classes.img} alt="price" />
        </Grid>

        <Grid>
          <Typography className={classes.text1}>VLX Price</Typography>

          <Bluetool title="View Historical VLX Price">
            <div style={{ display: 'flex', cursor: 'pointer' }}>
              <Typography className={classes.text2}>
                {!VLXPriceLoading ? `$${Math.round(VLXPrice * 1000) / 1000}` : <></>}
              </Typography>
              <Typography className={classes.text3}>
                {!BTCPriceLoading && !VLXPriceLoading ? (
                  `@ ${Math.round((VLXPrice / BTCPrice) * 10000000) / 10000000} BTC`
                ) : (
                  <></>
                )}
              </Typography>
              <Typography className={VLXPriceDiff >= 0 ? classes.priceHigh : classes.priceLow}>
                {!VLXPriceLoading ? VLXPriceDiff > 0 ? `${VLXPriceChange}` + '%' : `${VLXPriceChange}` + '%' : <></>}
              </Typography>
            </div>
          </Bluetool>
        </Grid>
      </Grid>

      <Divider variant="middle" className={classes.divider} />

      <Grid container alignItems="center">
        <Grid>
          <img src="/images/Home/HomeBanner/market.svg" className={classes.img} alt="market" />
        </Grid>

        <Grid>
          <Typography className={classes.text1}>VLX MARKET CAP ON VLX</Typography>

          <Bluetool title="Market cap of VLX on Velas, click Learn more">
            <div style={{ display: 'flex', cursor: 'pointer' }}>
              <Typography className={classes.text2}>
                {!VLXPriceLoading ? `$${commaNumber(Math.round(VLXPrice * VLXTotalSupply * 1000) / 1000)}` : <></>}
              </Typography>
              <Typography className={classes.text3}>({commaNumber(VLXTotalSupply)} VLX)</Typography>
            </div>
          </Bluetool>
        </Grid>
      </Grid>
    </div>
  )
}

const mapStateToProps = (state: AppState) => ({
  VLXPriceLoading: state.price.VLXPriceLoading,
  VLXPriceError: state.price.VLXPriceError,
  VLXPrice: state.price.VLXPriceSuccessResponse,
  VLXPriceChange: state.price.VLXPriceChange,
  VLXPriceDiff: state.price.VLXPriceDiff,
  VLXTotalSupply: state.price.VLXTotalSupply,
  BTCPriceLoading: state.price.BTCPriceLoading,
  BTCPriceError: state.price.BTCPriceError,
  BTCPrice: state.price.BTCPriceSuccessResponse,
})

export default connect(mapStateToProps, { getVLXPrice, getBTCPrice })(Price)
