import React, { useState, useEffect } from 'react'
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'

import { getSearchreResults, SearchAll } from '../../store/actions/search'
import useAutocomplete from '@material-ui/lab/useAutocomplete'
import { Button, FormControl, NativeSelect, Avatar } from '@material-ui/core'
import { useStyles, SearchInput, searchStyles, SearchSelect } from './style'
import { AppState } from '../../store/configureStore'
import makeBlockie from 'ethereum-blockies-base64'
import velas from '../../assets/images/VelasIcon.png'

interface ViewSearchProps {
  getSearchreResults: (searchIndex: any) => void
  SearchAll: (searchIndex: any) => void
  tokens: any
  addresses: any
}

function SearchBox({ getSearchreResults, SearchAll, tokens, addresses }: ViewSearchProps) {
  const classes = useStyles()
  const classes1 = searchStyles()
  const history = useHistory()

  const fields = ['All Filters', 'Addresses', 'Tokens', 'Name Tags', 'Labels', 'Websites']
  const [searchBy, setSearchBy] = useState('All Filters')
  const [searchIndex, setsearchIndex] = useState('')
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>): Promise<void> => {
    e.preventDefault()
    getSearchreResults(searchIndex)
  }
  useEffect(() => {
    SearchAll(searchIndex)
  }, [searchIndex])

  const { getRootProps, getInputProps, getListboxProps, getOptionProps, groupedOptions } = useAutocomplete({
    id: 'use-autocomplete-demo',
    options: tokens?.length > 0 ? tokens : addresses,
    getOptionLabel: (option: any) => (tokens?.length > 0 ? option.name : option.address),
  })

  const handleClick = (option: any) => {
    tokens?.length > 0 ? history.push(`/token/${option?.address}`) : history.push(`/address/${option?.address}`)
  }

  return (
    <form onSubmit={handleSubmit} className={classes.form}>
      <FormControl className={classes.selectForm}>
        <NativeSelect
          IconComponent={KeyboardArrowDownIcon}
          value={searchBy}
          onChange={(e) => setSearchBy(e.target.value as string)}
          input={<SearchSelect />}
          classes={{
            icon: classes.iconType,
          }}
        >
          {fields.map((field) => (
            <option key={field} value={field}>
              {field}
            </option>
          ))}
        </NativeSelect>
      </FormControl>
      {/* <FormControl className={classes.inputForm}> */}
      <FormControl className={classes.inputForm} {...getRootProps()}>
        {/* <div {...getRootProps()}> */}
        <SearchInput
          {...getInputProps()}
          placeholder="Search by Address / Txn Hash / Block / Token"
          onChange={(e) => setsearchIndex(e.target.value)}
          value={searchIndex}
        />
        {/* </div> */}
        {groupedOptions.length > 0 ? (
          <div style={{ position: 'absolute', top: 47, left: 0, width: '100%', zIndex: 6 }}>
            <ul className={classes1.listbox} {...getListboxProps()}>
              <li className={classes.labelTxt}>{tokens?.length > 0 ? 'Tokens' : 'Addresses'}</li>
              {groupedOptions.map((option: any, index) => (
                <li {...getOptionProps({ option, index })} onClick={() => handleClick(option)} className={classes.list}>
                  {tokens?.length > 0 ? (
                    <span style={{ display: 'flex' }}>
                      <span>
                        {option.image !== '' ? (
                          <Avatar src={option.image} className={classes.tokenImg} alt="token icon" />
                        ) : (
                          <Avatar src={velas} className={classes.tokenImg} alt="token icon" />
                        )}
                      </span>
                      <span>
                        {option?.name}&nbsp;({option?.symbol})&nbsp;
                        <span>
                          {option?.price && <span className={classes.price}>$&nbsp;{option?.price?.toFixed(2)}</span>}
                          <br />
                          <span style={{ fontSize: '13px' }}>{option?.address}</span>
                        </span>
                      </span>
                    </span>
                  ) : (
                    <span>
                      <img src={makeBlockie(option?.address)} className={classes.contractImg} alt="A" />
                      &nbsp;
                      <span>{option?.address}</span>
                    </span>
                  )}
                </li>
              ))}
            </ul>
          </div>
        ) : null}
        {/* <SearchInput
          placeholder="Search by Address / Txn Hash / Block / Token"
          value={searchIndex}
          onChange={(e) => setsearchIndex(e.target.value)}
        /> */}
      </FormControl>
      <Button type="submit" variant="contained" className={classes.submitBtn} disableRipple>
        <i className="fa fa-search"></i>
      </Button>
    </form>
  )
}

const mapStateToProps = (state: AppState) => ({
  tokens: state.search.tokens,
  addresses: state.search.addresses,
})

export default connect(mapStateToProps, { getSearchreResults, SearchAll })(SearchBox)
