import React from 'react'
import { connect } from 'react-redux'
import { AppState } from '../../../store/configureStore'
//material-ui components
import { TableRow, TableCell, Typography, Paper, TableContainer } from '@material-ui/core'
import ArrowRightAltOutlinedIcon from '@material-ui/icons/ArrowRightAltOutlined'
//style
import {
  StyledTable,
  StyledTableBody,
  StyledTableCell,
  StyledTableHead,
  StyledLink,
  StyledTextOverflow,
  StyledIconButton,
  StyledSvgIcon,
  StyledTooltip,
  StyledEmptyRowBox,
} from '../Style'

interface CustomizedTableProps {
  transaction: any
  columns: string[]
}

function CustomizedTable({ transaction, columns }: CustomizedTableProps) {
  return (
    <TableContainer component={Paper} style={{ boxShadow: 'none' }}>
      <StyledTable aria-label="custom pagination table">
        <StyledTableHead>
          <TableRow>
            {columns.map((column: any, key: any) => {
              return <StyledTableCell key={key}>{column}</StyledTableCell>
            })}
          </TableRow>
        </StyledTableHead>
        <StyledTableBody>
          {transaction ? (
            transaction.map((internalTxn: any, key: any) => {
              return (
                <TableRow key={key}>
                  <StyledTableCell>
                    {internalTxn?.call_type}
                    {internalTxn?.trace_address?.length > 0 && '_' + internalTxn?.trace_address?.join('_')}
                  </StyledTableCell>
                  <StyledTableCell>
                    <StyledLink to={`/address/${internalTxn?.from_address_hash}`}>
                      <StyledTooltip title={internalTxn?.from_address_hash} arrow>
                        <StyledTextOverflow>{internalTxn?.from_address_hash}</StyledTextOverflow>
                      </StyledTooltip>
                    </StyledLink>
                  </StyledTableCell>
                  <StyledTableCell>
                    <StyledIconButton>
                      <StyledSvgIcon>
                        <ArrowRightAltOutlinedIcon />
                      </StyledSvgIcon>
                    </StyledIconButton>
                  </StyledTableCell>
                  <StyledTableCell>
                    <StyledLink to={`/address/${internalTxn?.to_address_hash}`}>
                      <StyledTooltip title={internalTxn?.to_address_hash} arrow>
                        <StyledTextOverflow>{internalTxn?.to_address_hash}</StyledTextOverflow>
                      </StyledTooltip>
                    </StyledLink>
                  </StyledTableCell>
                  <StyledTableCell>
                    {(internalTxn?.value / Math.pow(10, 18)).toFixed(18).replace(/\.?0+$/, '')} VLX
                  </StyledTableCell>
                  <StyledTableCell style={{ color: '#77838f', fontSize: '80%' }}>
                    {internalTxn?.gas_used}
                  </StyledTableCell>
                </TableRow>
              )
            })
          ) : (
            <TableRow>
              <TableCell style={{ padding: '10px' }} colSpan={12}>
                <StyledEmptyRowBox>
                  <Typography style={{ fontSize: '14px' }}>There are no matching entries</Typography>
                </StyledEmptyRowBox>
              </TableCell>
            </TableRow>
          )}
        </StyledTableBody>
      </StyledTable>
    </TableContainer>
  )
}

const mapStateToProps = (state: AppState) => ({
  transaction: state.transaction.internalTxns,
})

export default connect(mapStateToProps)(CustomizedTable)
