import { createBrowserHistory } from 'history'

import axiosInstance from '../../axios.config'
import { GetSearch, GetSearchRequest, GetSearchError, RealSearch } from './action.types'
import { Dispatch } from 'redux'

import { BackendURL } from '../../config/config'
export const browserHistory = createBrowserHistory()
const CoinGecko = require('coingecko-api')
const coinGeckoClient = new CoinGecko()

export const getSearchreResults =
  (searchIndex: any) => async (dispatch: Dispatch<GetSearchRequest | GetSearch | GetSearchError>) => {
    try {
      const response = await axiosInstance.get<any>(`${BackendURL}/search`, {
        params: {
          searchIndex: searchIndex,
        },
      })

      const { searchResult } = response.data

      if (searchResult) {
        browserHistory.push(`/${searchResult.type}/${searchResult.searchResult}`)
        window.location.reload()
      } else {
        browserHistory.push('/search')
        window.location.reload()
      }
    } catch (error: any) {
      dispatch({
        type: 'GET_SEARCH_ERROR',
        payload: {
          error: 'error',
        },
      } as GetSearchError)
    }
  }

export const SearchAll = (searchIndex: any) => async (dispatch: Dispatch<RealSearch | GetSearchError>) => {
  try {
    const response = await axiosInstance.get<any>(`${BackendURL}/realsearch/${searchIndex}`)
    const foundList = await coinGeckoClient.coins.list()
    const coinList = foundList.data
    let tokenList = []
    let coinIdList = []
    let marketCapList
    let found
    if (response.data.data.tokens?.length > 0) {
      for (let i = 0; i < response.data.data.tokens?.length - 1; i++) {
        found = coinList.find(
          (coin: any) => coin?.symbol?.toLowerCase() === response.data.data.tokens[i]?.symbol?.toLowerCase(),
        )
        if (found !== undefined) {
          coinIdList.push(found.id)
        }
      }
      const marketData = await coinGeckoClient.coins.markets({ ids: coinIdList })
      marketCapList = marketData.data

      for (let i = 0; i < response.data.data.tokens?.length; i++) {
        const found = marketCapList.find(
          (coin: any) => coin?.symbol?.toLowerCase() === response.data.data.tokens[i]?.symbol?.toLowerCase(),
        )
        if (found)
          tokenList.push({
            name: response.data.data.tokens[i].name,
            address: response.data.data.tokens[i].address,
            symbol: response.data.data.tokens[i].symbol,
            image: found.image,
            type: response.data.data.tokens[i].type,
            holders: response.data.data.tokens[i].holders,
            price: found.current_price,
            change: found.price_change_percentage_24h,
            volume: found.total_volume,
            marketcap: found.market_cap,
          })
        else
          tokenList.push({
            name: response.data.data.tokens[i].name,
            address: response.data.data.tokens[i].address,
            symbol: response.data.data.tokens[i].symbol,
            image: '',
            type: response.data.data.tokens[i].type,
            holders: response.data.data.tokens[i].holders,
            price: '',
            change: '',
            volume: '',
            marketcap: '',
          })
      }
    }
    dispatch({
      type: 'SEARCH_RESULT',
      payload: { tokens: tokenList, addresses: response.data.data.addresses },
    })
  } catch (error: any) {
    console.log(error)
    dispatch({
      type: 'GET_SEARCH_ERROR',
      payload: {
        error: 'error',
      },
    } as GetSearchError)
  }
}
