import { createStyles, Theme, makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      marginTop: '-80px',
    },
    latestBlock: {
      display: 'block',
      [theme.breakpoints.down(500)]: {
        display: 'none',
      },
    },
    section: {
      alignItems: 'left',
      justifyContent: 'left',
      marginBottom: '30px',
      textAlign: 'center',
      backgroundColor: `${localStorage.appTheme === 'darkTheme' ? '#212121' : ' #22223a'}`,
      // backgroundImage: `url('/images/abstract-shapes-20.png')`,
      backgroundPosition: 'center',
      padding: '30px 15px 20px 15px',
    },
    title: {
      color: '#FFF',
      fontSize: '1.75rem',
      fontWeight: 500,
      lineHeight: 1.5,
      margin: '15px 15px',
      textAlign: 'left !important',
    },
    search: {
      width: '100%',
      margin: 'auto',
    },
    adimg: {
      borderRadius: '1rem',
      alignItems: 'center',
      zIndex: 3,
      width: '250px',
      [theme.breakpoints.down(1200)]: {
        marginTop: '29px',
      },
    },
  }),
)

export default useStyles
