import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { AppState } from '../../../../../store/configureStore'

//material-ui components
import {
  TableBody,
  TableRow,
  Box,
  Paper,
  TableContainer,
  Table,
  TableHead,
  Typography,
  Chip,
  Button,
} from '@material-ui/core'
//components
import TablePaginationActions from './TablePagination'
import { isEmptyObject } from '../../../../../common/utils'
import SearchBtn from '../../../Components/SearchBox/SearchBox'
import { updateTokenState, getAllToken } from '../../../../../store/actions/admin'

//style
import {
  StyledTable,
  StyledTableCell,
  StyledTableCell2,
  StyledTableHead,
  StyledTablePagination,
  StyledRowsPerPageBox,
  BootstrapInput,
  StyledNativeSelect,
  StyledTableControlBox,
  useStyles,
  StyledEmptyRowBox,
} from '../../../styles'
import Alert from '../../../../../components/Alert'

interface CustomizedTableProps {
  tableInfo: () => void
  updateTokenState: ( tokenAddress: any ) => void
  getAllToken: (page: any, rowsPerPage: any) => void
  rowsPerPage: number
  page: number
  rows: any
  totalCount: number
  columns: string[]
  error: string
  updateTokenStateSuccess: boolean
  handleChangePage: (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => void
  handleChange: (event: React.ChangeEvent<{ value: unknown }>) => void
}

const CustomizedTable: React.FC<CustomizedTableProps> = ({
  tableInfo,
  updateTokenState,
  getAllToken,
  rowsPerPage,
  page,
  rows,
  columns,
  totalCount,
  error,
  updateTokenStateSuccess,
  handleChange,
  handleChangePage,
}) => {
  const classes = useStyles()
  const [openAlert, setOpenAlert] = useState(false)
  const [alertContent, setAlertContent] = useState('')
  const [alertType, setAlertType] = useState('')

  const onPageChange = (event: React.MouseEvent<HTMLButtonElement> | null, newPage: number) => {
    handleChangePage(event, newPage)
  }
  const onSelectChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    handleChange(event)
  }

  const handleCloseAlert = () => {
    setOpenAlert(false)
  }

  const onHandleCheck = (tokenAddress: string) => (event: React.KeyboardEvent | React.MouseEvent) => {
    updateTokenState(tokenAddress)
  }

  useEffect(() => {
    if (error) {
      setAlertContent(error)
      setOpenAlert(true)
      setAlertType('error')
    } else if (updateTokenStateSuccess) {
      setAlertContent('Successfully Update Token Data')
      setOpenAlert(true)
      setAlertType('success')

      getAllToken(page, rowsPerPage)
    }
  }, [error, updateTokenStateSuccess])

  return (
    <div style={{ marginTop: '10px' }}>
      <div>
        <Box mb="20px" display="flex" justifyContent="space-between">
          <SearchBtn placeholder="Search Name and Email" />
        </Box>
      </div>
      <Alert openAlert={openAlert} alertContent={alertContent} closeAlert={handleCloseAlert} alertType={alertType} />
      <StyledTableControlBox mb="20px">
        <Box>{tableInfo}</Box>
        <Box>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <StyledTablePagination
                  colSpan={3}
                  rowsPerPage={rowsPerPage}
                  count={totalCount}
                  page={page}
                  onPageChange={onPageChange}
                  ActionsComponent={TablePaginationActions}
                />
              </TableRow>
            </TableHead>
          </Table>
        </Box>
      </StyledTableControlBox>
      <TableContainer component={Paper} style={{ boxShadow: 'none' }}>
        <StyledTable aria-label="custom pagination table">
          <StyledTableHead>
            <TableRow>
              {columns.map((column: any, key: any) => {
                if (column === 'response' || column === 'status') {
                  return <StyledTableCell2 key={key}>{column}</StyledTableCell2>
                } else {
                  return <StyledTableCell key={key}>{column}</StyledTableCell>
                }
              })}
            </TableRow>
          </StyledTableHead>
          <TableBody>
            {!isEmptyObject(rows) &&
              rows.map((row: any, key: any) => {
                return (
                  <TableRow key={key}>
                    <StyledTableCell>{key + 1}</StyledTableCell>
                    <StyledTableCell>{row.address}</StyledTableCell>
                    <StyledTableCell>{row.name}</StyledTableCell>
                    <StyledTableCell>{row.symbol}</StyledTableCell>
                    <StyledTableCell>{row.type}</StyledTableCell>
                    <StyledTableCell2>
                      {row.isShown === "true" ? (
                        <Chip label="Checked" className={classes.checkedFeedback} />
                      ) : (
                        <Chip label="Unchecked" className={classes.uncheckedFeedback} />
                      )}
                    </StyledTableCell2>
                    <StyledTableCell>
                      <div style={{ display: 'flex', justifyContent: 'center', width: 'max-content' }}>
                        {/* <Link to={`/admin/tokenInfo/${row.address}`} style={{ flexDirection: 'row' }}> */}
                        <Button href={`/admin/token/tokenInfo/${row.address}`} className={classes.viewBtn} disableRipple>
                          <span>View</span>
                        </Button>
                        <Button onClick={onHandleCheck(row.address)} className={classes.viewBtn} disableRipple>
                          <i className="fas fa-check-circle"></i>&nbsp;Check&nbsp;
                        </Button>
                        {/* </Link> */}
                      </div>
                    </StyledTableCell>
                  </TableRow>
                )
              })}
            {!isEmptyObject(rows) && rows.length === 0 && (
              <TableRow>
                <StyledTableCell style={{ padding: '10px' }} colSpan={12}>
                  <StyledEmptyRowBox>
                    <Typography style={{ fontSize: '14px' }}>There are no matching entries</Typography>
                  </StyledEmptyRowBox>
                </StyledTableCell>
              </TableRow>
            )}
          </TableBody>
        </StyledTable>
      </TableContainer>
      <StyledTableControlBox my="12px">
        <Box style={{ display: 'flex', paddingLeft: '20px' }}>
          <StyledRowsPerPageBox>
            Show
            <StyledNativeSelect
              id="demo-customized-select-native"
              value={rowsPerPage}
              onChange={onSelectChange}
              input={<BootstrapInput />}
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </StyledNativeSelect>
            Records
          </StyledRowsPerPageBox>
        </Box>
        <Box>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <StyledTablePagination
                  colSpan={3}
                  count={totalCount}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onPageChange={onPageChange}
                  ActionsComponent={TablePaginationActions}
                />
              </TableRow>
            </TableHead>
          </Table>
        </Box>
      </StyledTableControlBox>
    </div>
  )
}

const mapStateToProps = (state: AppState) => ({
  updateTokenStateSuccess: state.admin.updateTokenStateSuccess,
  error: state.admin.TokenInfoError,
})

export default  connect(mapStateToProps, { updateTokenState, getAllToken })(CustomizedTable)
