import React from 'react'
import copy from 'copy-text-to-clipboard'
import Box from '@material-ui/core/Box'
// import { useStyles } from '../../../styles'
import { Typography } from '@material-ui/core'
import { useParams } from 'react-router'
import ContractMoreOptionsDropDown from './ContractMoreOptionsDropDown'
import Grid from '@material-ui/core/Grid'
import { ContractCodeBtn } from '../../../styles'
import { IconTooltip } from '../../../../Resource/TopStatistics/components/ViewPanel/PanelBody/PanelItem'

import AceEditor from 'react-ace'

import 'ace-builds/src-noconflict/mode-java'
import 'ace-builds/src-noconflict/theme-github'
import 'ace-builds/src-noconflict/ext-language_tools'
import 'ace-builds/webpack-resolver'

import { DeployedByteCodeSourceMapCode } from './CodeExample'

import { ContractABI } from './ContractABI'
import { ContractCreationCode } from './ContractCreationCode'
import { DeployedByteCodeSourceMap } from './DeployedByteCodeSourceMap'
interface ContractProps {
  contractInfo: any
  loading: boolean
}

function SmartContractCodeViewer({ contractInfo, loading }: ContractProps) {
  const { address } = useParams<any>()
  // const code = 'Smart contract code'
  console.log('contractInfo on frontend', contractInfo)
  const [sourceCode, setSourceCode] = React.useState({ single: '', multiple: {} })
  const [abi, setAbi] = React.useState('')
  const [creationCode, setCreationCode] = React.useState('')
  const handleCopyCode = (code: string) => {
    copy(code)
    alert('Source code copied to clipboard')
  }
  React.useEffect(() => {
    if (!loading) {
      setSourceCode(contractInfo['sourceCode'])
      setAbi(contractInfo['abi'])
      setCreationCode(contractInfo['creationCode'])
    }
  }, [address, loading, contractInfo])

  return !loading ? (
    <Box>
      <Box>
        <Grid container>
          <Grid item xs={12} sm={6} style={{ alignSelf: 'center' }}>
            <Typography variant="body2">
              <i className="far fa-file-code"></i>
              &nbsp;
              <strong>Contract Source Code</strong>
              (Soldity language format)
            </Typography>
          </Grid>
          <Grid item xs={12} sm={6}>
            <div style={{ float: 'right', display: 'flex' }}>
              <ContractMoreOptionsDropDown />
            </div>
          </Grid>
        </Grid>
      </Box>
      <Box mt={2}>
        {sourceCode['single'] && (
          <>
            <Grid container>
              <Grid item xs={12} sm={6} style={{ alignSelf: 'center' }}></Grid>
              <Grid item xs={12} sm={6}>
                <div style={{ float: 'right', display: 'flex' }}>
                  <IconTooltip title="Copy source code to clipboard">
                    <ContractCodeBtn onClick={() => handleCopyCode(sourceCode['single'])}>
                      <i className="far fa-copy"></i>
                    </ContractCodeBtn>
                  </IconTooltip>
                  &nbsp;
                  <IconTooltip title="Toggle fullscreen">
                    <ContractCodeBtn>
                      <i className="fas fa-compress"></i>
                    </ContractCodeBtn>
                  </IconTooltip>
                </div>
              </Grid>
            </Grid>
            <AceEditor
              theme="github"
              // onChange={onChange}
              name="contractCodeViewer"
              editorProps={{ $blockScrolling: true }}
              setOptions={{
                enableBasicAutocompletion: true,
                enableLiveAutocompletion: true,
                enableSnippets: true,
              }}
              width="100%"
              height="250px"
              value={sourceCode['single']}
              readOnly={true}
              wrapEnabled={true}
              mode="java"
              showPrintMargin={false}
            />
          </>
        )}
        {sourceCode['multiple'] &&
          Object.keys(sourceCode['multiple']).map((key, index) => (
            <Box mt={2}>
              <Box mt={1} mb={1}>
                <Grid container>
                  <Grid item xs={12} sm={6} style={{ alignSelf: 'center' }}>
                    File {index + 1} of {Object.keys(sourceCode['multiple']).length} :&nbsp; {key}
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <div style={{ float: 'right', display: 'flex' }}>
                      <IconTooltip title="Copy source code to clipboard">
                        <ContractCodeBtn onClick={() => handleCopyCode(sourceCode['multiple'][key])}>
                          <i className="far fa-copy"></i>
                        </ContractCodeBtn>
                      </IconTooltip>
                      &nbsp;
                      <IconTooltip title="Toggle fullscreen">
                        <ContractCodeBtn>
                          <i className="fas fa-compress"></i>
                        </ContractCodeBtn>
                      </IconTooltip>
                    </div>
                  </Grid>
                </Grid>
              </Box>
              <Box mt={1} mb={1}>
                <AceEditor
                  theme="github"
                  // onChange={onChange}
                  name="contractCodeViewer"
                  editorProps={{ $blockScrolling: true }}
                  setOptions={{
                    enableBasicAutocompletion: true,
                    enableLiveAutocompletion: true,
                    enableSnippets: true,
                  }}
                  width="100%"
                  height="250px"
                  value={sourceCode['multiple'][key] as string}
                  readOnly={true}
                  wrapEnabled={true}
                  mode="java"
                  showPrintMargin={false}
                />
              </Box>
            </Box>
          ))}
      </Box>
      <Box mt={2}>
        <ContractABI abi={abi} />
      </Box>
      <Box mt={2}>
        <ContractCreationCode code={creationCode} />
      </Box>
      <Box mt={2}>
        <DeployedByteCodeSourceMap code={DeployedByteCodeSourceMapCode} />
      </Box>

      {/* <Box mt={2}>
        <SwarmSource code={swarmSource} />
      </Box> */}
    </Box>
  ) : (
    <div>Loading...</div>
  )
}

export default SmartContractCodeViewer
